@use "global" as *;

.p-target {
  position: relative;
  background: $white;
  padding-top: rem(48);
  @include mq(md) {
    padding-top: initial;
  }
}
.p-target::before {
  content: attr(data-company) "";
  position: absolute;
  text-transform: uppercase;
  font-weight: 700;
  color: $skyBlue;
  top: 0;
  left: 0;
  z-index: 3;
  font-size: rem(48);
  font-family: $second-font-family;
  @include mq(md) {
    font-size: rem(138);
    z-index: 0;
  }
}
.p-target__content {
  @include mq(md) {
    display: flex;
  }
}
.p-target__jp {
  margin-top: rem(12);
  @include mq(md) {
    margin-top: rem(27);
  }
}
.p-target__title-wrapper {
  max-width: rem(428);
  width: 100%;
  @include mq(md) {
    max-width: rem(510);
    margin-top: rem(120);
  }
}
.p-target__title {
  @include mq(md) {
    position: relative;
    z-index: 10;
  }
}
.p-target__subTitle {
  margin-top: rem(8);
  @include mq(md) {
    margin-top: rem(24);
  }
}
.p-target__btn {
  margin-top: rem(24);
  @include mq(md) {
    margin-top: rem(48.75);
  }
}
.p-target__img {
  margin-top: rem(30);
  position: relative;
  z-index: 0;
  @include mq(md) {
    width: 58vw;
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    margin-left: auto;
  }
}
.p-target__img::before {
  padding-top: 58.03%;
  display: block;
  content: "";
}
.p-target__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}


.p-target__hover:hover .c-btn::before {
  width: 37.5px;
  height: 37.5px;
  background: url(../images/common/btn_box_reverse.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border: 1px solid $blue;
  z-index: 3;
}
.p-target__hover:hover .c-btn::after {
  background: url(../images/common/btn_arrow.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 97px;
  height: 6px;
  left: 9px;
}